import axios from 'axios'
import { Link } from 'gatsby'
import React, { memo, useEffect, useState } from 'react'
import { useRendersCount, useUpdate } from 'react-use'
import { Helmet } from 'react-helmet'
import ym from 'react-yandex-metrika'
import { useLocation } from '@reach/router'
import { useForm, Controller } from 'react-hook-form'
import * as yup from 'yup'
import { yupResolver } from '@hookform/resolvers/yup'

import { FORM_ID } from '~consts'
import { Button, TextArea, TextInput, Checkbox } from '~ui'
import { Container } from '~ux'

interface IFormInputs {
  name: string
  message?: string
  email: string
}

const requiredMessage = '   '

const schema = yup.object().shape({
  name: yup.string().required(requiredMessage),
  email: yup.string().email('  e-mail').required(requiredMessage),
  agreement: yup.bool().oneOf([true], requiredMessage)
})

const addAnalitycs = (pathname: string) => {
  ym('reachGoal', 'SubmitApplication')

  switch (pathname) {
    case '/':
      ym('reachGoal', 'SubmitLeadgen')
    case '/landings':
      ym('reachGoal', 'SubmitPortal')
  }
}

interface FooterProps {}

const FooterProps: React.FC<FooterProps> = props => {
  const {} = props
  const update = useUpdate()

  const { pathname } = useLocation()
  const rendersCount = useRendersCount()

  useEffect(() => {
    if (rendersCount === 1) {
      update()
    }
  }, [rendersCount])

  const [isDisable, setIsDisable] = useState<boolean>(false)

  const {
    register,
    handleSubmit,
    control,
    formState: { errors }
  } = useForm({
    mode: 'onChange',
    resolver: yupResolver(schema)
  })
  const onSubmit = (data: IFormInputs) => {
    setIsDisable(true)
    sendForm(data)
  }

  const sendForm = ({ name, email, message }: IFormInputs) => {
    addAnalitycs(pathname)
    return axios.post(`https://backend.honeyleads.ru/api/landing-requests`, { name, email, message })
  }

  return (
    <>
      <div className="md:bg-none bg-cover bg-footer relative bg-no-repeat pb-60 pt-60">
        <Helmet>
          <script
            id="amoforms_script_857395"
            async
            src="https://forms.amocrm.ru/forms/assets/js/amoforms.js?1635958282"
          />
        </Helmet>
        <Container className="relative mt-40 z-10">
          <div className="text-32 leading-40 font-bold text-purple md:text-40 xs:text-22 xs:leading-26">
             
            <br />
            <span className="text-[#FF7143] xs:text-[#28D2AF]">    </span>
          </div>
          <div className="max-w-[50vw] text-16 text-left text-purple/60 leading-24 md:max-w-[100vw] mt-16">
              ,       
          </div>
          <form className="mt-40 max-w-[33.125rem] md:mx-auto" onSubmit={handleSubmit(onSubmit)} id={FORM_ID}>
            <div className="relative">
              <TextInput {...register('name')} name="name" placeholder="" variant="footer" disabled={isDisable} />
              <div className="absolute text-[#d11507] text-10 pt-5">{errors.name?.message}</div>
            </div>

            <div className="mt-30 relative">
              <TextInput
                {...register('email')}
                name="email"
                placeholder=" "
                variant="footer"
                disabled={isDisable}
              />
              <div className="absolute text-[#d11507] text-10 pt-5">{errors.email?.message}</div>
            </div>

            <div className="mt-30 relative">
              <TextArea
                {...register('message')}
                variant="footer"
                name="message"
                placeholder=""
                disabled={isDisable}
              />
            </div>

            {isDisable && <h4 className="text-20 text-center mt-30">,    !</h4>}

            <div className="mt-24 flex align-center">
              <Controller
                name="agreement"
                control={control}
                defaultValue={false}
                render={({ field }) => <Checkbox {...field} className="mr-10" />}
              />
              <div className={`text-14 ${errors?.agreement ? 'text-[#d11507]' : 'text-purple'}`}>
                 {' '}
                <Link to="/agreement" className="underline">
                      
                </Link>
              </div>
            </div>

            <Button
              className="text-[#FFFFFF] mb-60 mt-30 w-full disabled:opacity-75"
              variant="primary"
              disabled={isDisable}
              onClick={() => {}}
            >
              
            </Button>
          </form>
        </Container>
      </div>

      <footer className="bg-[#222]">
        <Container>
          <div className="flex justify-between mt-32 mb-32 text-14 font-light text-[#C5C5C5] leading-28">
             ,
            <br />
            125047,  ,  1- ,  35,  501
            <br />
             9710082903  771001001  1207700149414
            <div className="text-right">
              <Link to="/agreement" className="underline">
                C    
              </Link>
              <br />
              <a href="mailto:hi@honeyleads.com" className="text-[#FF7143]">
                hi@honeyleads.com
              </a>
            </div>
          </div>
        </Container>
      </footer>
    </>
  )
}

export default memo(FooterProps)
